body {
  background-color: #1f1e1e;
  color : white;
  overflow: hidden;
}

h1{
  text-align: center;
}

.todo-wrapper {
  background: #353434;
  padding : 2%;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0px, 5px, 7px black;
}

.todo-input {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid grey;
  padding-bottom: 25px;
  margin-bottom: 25px;
}
.todo-input-item{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-right: 25px;
}


.todo-input-item label{
  font-weight: bold;
  margin-bottom: 10px;
}

.todo-input-item input{
  padding: 8px;
  border: none;
  width: 250px;
}

.todo-input-item input:focus{
  outline: 2px solid rgb(0,230,122);
}

.primaryBtn{
  background-color: rgb(0, 230,122);
  color: white;
  border-radius: 0px;
  margin-top: 25px;
  padding: 10px;
  width: 60px;
  cursor: pointer;
}

.primaryBtn:hover{
  background-color: rgb(6, 121, 67);
}

.secondaryBtn{
  background-color: rgb(71, 71, 71);
  color: white;
  border-radius: 0px;
  margin-top: 25px;
  padding: 10px;
  width: fit-content;
  cursor: pointer;
}
.active{
  background-color: rgb(0, 230,122);
}

.todo-list{
  display: flex;
  flex-direction: column;
}

.btn-area{
    margin-bottom: 15px;
}

.todo-list-item{
  background-color: rgb(66, 62, 62);
  display: flex;
  justify-content: space-between;
  padding: 25px;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-bottom: 10px;
  box-shadow: 0px, 3px, 5px gray;
}

.todo-list-item h3{
  font-size: 25px;
  color: rgb(0,230,122);
 font-weight: bold;
 margin: 0;
}

.todo-list-item p{
  font-size: 14px;
  color: rgb(161,161,161);
  margin-top: 8px;
}

.icon{
  font-size: 35px;
  cursor: pointer;
}

.icon:hover{
  color: red;
}

.check-icon{
  font-size: 33px;
  margin-left: 10px;
  color: rgb(14, 182, 104);
}

.check-icon:hover{
  color: rgb(54, 255, 175);
}